import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Home from '@/components/Home'
import Empbasis from '@/components/emp/Empbasis'
import SysAuthority from '@/components/system/SysAuthority'
import Paquerypatient from '@/components/patient/Paquerypatient'

Vue.use(Router)

export default new Router({
  routes: [

  {
     path: '/patient/Paquerypatient',
     name: 'Paquerypatient',
     component: Paquerypatient
   },

    {
      path: '/',
      name: 'Login',
      component: Login
    },

    {
      path: '/home',
      name: '主页',
      component: Home,
      hidden: true ,
      meta: {
        requireAuth: true
      },
      children:[]
    }

  ]
})



